.backgrounds {
  height: 100%;
  position: relative;

  &__main {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;

    background-image: url(/images/landing_page_test_back.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }

  &__sparks-back {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;

    background-image: url(/images/sparks_back.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center left;
  }

  &__witcher {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;

    background-image: url(/images/witcher.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top left;
  }
}
